import {
	myRequestGet
} from '@/utils/zgrequest.js'
import parse from '@/utils/htmlparser.js';
export default {
	data() {
		return {
			id: "",
			newsDetail: {},
			htmlNodes: []
		}
	},
	onLoad(options) {
		this.id = options.id;
		this.getNewsDetail()
	},
	methods: {
		async getNewsDetail() {
			const res = await myRequestGet('/api/getnew/' + this.id)
			this.newsDetail = res.message[0]


			this.htmlNodes = parse(this.newsDetail.content)

		}
	}
}
